import React from 'react'
import styled from 'styled-components'

const Section= styled.section`
min-height: 100vh;
width: 80vw;
margin: 0 auto;

display: flex;
justify-content: center
align-items: center;

position: relative;
`;



export default function Banner() {
  return (
  <Section>
    Banner
  </Section>
  )
}
